import moment from 'moment-timezone';
import React, { useEffect, useState } from 'react'

const InterviewCandidateDetail = (props) => {
    const { handleOpenClose, openModel,candidateIntervewData } = props
    const [activeTab, setActiveTab] = useState("personalDetails");
    const [candidateDetail,setCandidateDetail]=useState('')
    const [jobDescription,setJobDescription]=useState('')
    const [mrfDetail,setMrfDetail]=useState('')
    const handleActiveTab = (type) => {
        setActiveTab(type)
    }

    useEffect(()=>{
        if(candidateIntervewData){
            const {
                candidate_data_value,
                job_description_value,
                mrf_data_value
            } =candidateIntervewData?.[0]
            setCandidateDetail(candidate_data_value)
            setJobDescription(job_description_value)
            setMrfDetail(mrf_data_value)
        }
    },[candidateIntervewData])
    
    return (
        <>
            <div className={`offcanvas offcanvas-end  ${openModel ? 'show' : ''}`}
                tabIndex={-1}
                style={{
                    visibility: openModel ? 'visible' : 'hidden',
                    width: "800px"
                }}
                id="interviewDetail"
            >
                <div className="offcanvas-header">
                    <h5 className="offcanvas-title  fs-5 fw-bold text-primary">Candidate Details</h5>
                    <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
                        onClick={() => handleOpenClose()}
                    />
                </div>
                <div className="offcanvas-body myRequest ">
                    <div className="formPanel mrfMain">
                        <ul className="nav nav-tabs leaveTabs mb-4">
                            <li className="nav-item">
                                <button
                                    className={`nav-link ${activeTab === "personalDetails" ? "active" : ""}`}
                                    data-bs-toggle="tab"
                                    data-bs-target="#personalDetails"
                                    type="button"
                                    onClick={() => handleActiveTab("personalDetails")}
                                >
                                    Personal Details
                                </button>
                            </li>
                            <li className="nav-item">
                                <button
                                    className={`nav-link ${activeTab === "mrfDetails" ? "active" : ""}`}
                                    data-bs-toggle="tab"
                                    data-bs-target="#mrfDetails"
                                    type="button"
                                    onClick={() => handleActiveTab("mrfDetails")}
                                >
                                    MRF Detail
                                </button>
                            </li>
                            <li className="nav-item">
                                <button
                                    className={`nav-link ${activeTab === "jobDescription" ? "active" : ""}`}
                                    data-bs-toggle="tab"
                                    data-bs-target="#jobDescription"
                                    type="button"
                                    onClick={() => handleActiveTab("jobDescription")}
                                >
                                    Job Description
                                </button>
                            </li>


                        </ul>
                        {/* <ul className="nav nav-tabs leaveTabs mb-4">
                    <li className="nav-item">
                        <button className="nav-link active" data-bs-toggle="tab" data-bs-target="#personalDetails1"
                            type="button">Personal Details</button>
                    </li>
                    <li className="nav-item">
                        <button className="nav-link" data-bs-toggle="tab" data-bs-target="#mrfDetaiil2" type="button">MRF
                            Details</button>
                    </li>
                </ul> */}
               
                        <div className="tab-content">
                            <div className={`tab-pane fade ${activeTab === "personalDetails" ? "show active" : ""}`} id="personalDetails">
                                <div className="row">
                                    <div className="col-md-6 form-group mb-3">
                                        <label>Candidate Name</label>
                                        <input type="text" className="form-control" value={candidateDetail?.name||''} name="" id="" disabled />
                                    </div>
                                    <div className="col-md-6 form-group mb-3">
                                        <label>Candidate ID</label>
                                        <input type="text" className="form-control" value={candidateDetail?._id ||''} name="" id="" disabled />
                                    </div>
                                    <div className="col-md-6 form-group mb-3">
                                        <label>Email</label>
                                        <input type="text" className="form-control" value={candidateDetail?.email||''} name="" id=""
                                            disabled />
                                    </div>
                                    <div className="col-md-6 form-group mb-3">
                                        <label>Mobile</label>
                                        <input type="text" className="form-control" value={candidateDetail?.mobile||''} name="" id="" disabled />
                                    </div>
                                    <div className="col-md-12 form-group mb-3">
                                        <label>Address(with Pincode)</label>
                                        <input type="text" className="form-control"
                                            value={`${candidateDetail?.address,(candidateDetail?.pincode) ||''}`} name="" id="" disabled />
                                    </div>
                                    <div className="col-md-12 form-group mb-3">
                                        <label>Location</label>
                                        <input type="text" className="form-control"
                                            value={`${candidateDetail?.location ||''}`} name="" id="" disabled />
                                    </div>
                                    <div className="col-md-12 form-group mb-3">
                                        <label>Experience</label>
                                        <textarea name="" id="" className="w-100" 
                                            value={candidateDetail?.candidate_year_of_experience_name}
                                            disabled></textarea>
                                    </div>
                                    <div className="col-md-12 form-group mb-3">
                                        <label>Skills</label>
                                        <textarea name="" id="" className="w-100" 
                                            value={candidateDetail?.skills?.toString()||''}
                                            disabled></textarea>
                                    </div>
                                    <div>
                                        {/* <a href="#" className="btn btn-primary gradientBg">Download Resume</a> */}
                                    </div>
                                </div>
                            </div>
                            <div className={`tab-pane fade ${activeTab === "mrfDetails" ? "show active" : ""}`} id="mrfDetails">
                                <div className="row">
                                    <div className="col-md-12 form-group mb-3">
                                        <label>MRF ID</label>
                                        <input type="text" className="form-control" value={mrfDetail?._id ||''} name="" id="" disabled />
                                    </div>
                                    <div className="col-md-6 form-group mb-3">
                                        <label>Department Name</label>
                                        <input type="text" className="form-control" value={mrfDetail?.department_name ||''} name="" id=""
                                            disabled />
                                    </div>
                                    <div className="col-md-6 form-group mb-3">
                                        <label>Designation Name</label>
                                        <input type="text" className="form-control" value={mrfDetail?.designation_name ||''}  name="" id="" disabled />
                                    </div>
                                    <div className="col-md-6 form-group mb-3">
                                        <label>Position Name</label>
                                        <input type="text" className="form-control" value={mrfDetail?.position_name ||''}  name="" id="" disabled />
                                    </div>
                                    <div className="col-md-6 form-group mb-3">
                                        <label>Qualififcation </label>
                                        <input type="text" className="form-control" value={mrfDetail?.min_qualififcation_name ||''}  name="" id="" disabled />
                                    </div>
                                  
                                    <div className="col-md-12 form-group mb-3">
                                        <label>MRF Raised By</label>
                                        <input type="text" className="form-control" value={mrfDetail?.created_by_name ||''} name="" id=""
                                            disabled />
                                    </div>
                                    <div className="col-md-6 form-group mb-3">
                                        <label>Created At</label>
                                        <input type="text" className="form-control" value={moment(mrfDetail?.created_at).format("DD-MM-YYYY") || ''} name="" id=""
                                            disabled />
                                    </div>
                                    <div className="col-md-6 form-group mb-3">
                                        <label>MRF Status</label>
                                        <input type="text" className="form-control" value={mrfDetail?.mrf_status || ''} name="" id="" disabled />
                                    </div>
                                    <div className="col-md-12 form-group mb-3">
                                        <label>Preferred Qualification</label>
                                        <input type="text" className="form-control" value={mrfDetail?.preferred_qualification_name ||''} name="" id="" disabled />
                                    </div>
                                    <div className="col-md-12 form-group mb-3">
                                        <label>Year Of Experience </label>
                                        <input type="text" className="form-control" value={mrfDetail?.year_of_experience_name ||''} name="" id="" disabled />
                                    </div>
                                    
                                </div>
                            </div>
                            <div className={`tab-pane fade ${activeTab === "jobDescription" ? "show active" : ""}`} id="jobDescription">
                                <div className="row">
                                    <div className="col-md-12 form-group mb-3">
                                        <label>Responsiblities</label>
                                        <input type="text" className="form-control" value={jobDescription?.responsiblities||''} name="" id="" disabled />
                                    </div>
                                    <div className="col-md-12 form-group mb-3">
                                        <label>Domain Knowledge</label>
                                        <input type="text" className="form-control" value={jobDescription?.domain_knowledge} name="" id="" disabled />
                                    </div>
                                    <div className="col-md-12 form-group mb-3">
                                        <label>Other Skills </label>
                                        <input type="text" className="form-control" value={jobDescription?.other_skills||''} name="" id="" disabled />
                                    </div>
                                    <div className="col-md-12 form-group mb-3">
                                        <label>Training Certificates </label>
                                        <input type="text" className="form-control" value={jobDescription?.training_certificates_name||''} name="" id="" disabled />
                                    </div>
                                    <div className="col-md-12 form-group mb-3">
                                        <label>Role Summary</label>
                                        <textarea name="" id=""
                                         className="w-100"
                                         value={jobDescription?.role_summary||''}
                                            placeholder="dsfasdf"
                                            disabled></textarea>
                                    </div>

                                </div>
                            </div>

                        </div>
                    </div>
                </div>
                <div className="offcanvas-footer">
                    <div className="d-flex align-items-center justify-content-end gap-3">
                        <button type="button" className="btn btn-secondary">Back</button>
                    </div>
                </div>
            </div>
        </>
    )
}

export default InterviewCandidateDetail
