import React from 'react'
import Button from '../../commanComponent/Button'

const CandidateJobDescription = () => {
  return (
    <>
      <div class="offcanvas offcanvas-end" id="jobDescription">
        <div class="offcanvas-header">
            <h5 class="offcanvas-title  fs-5 fw-bold text-primary">Job Description</h5>
            <button type="button" class="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"></button>
        </div>
        <div class="offcanvas-body myRequest ">
            <div class="formPanel mrfMain">
                <div class="row">
                    <div class="col-md-6 form-group mb-3">
                        <label>MRF ID</label>
                        <input type="text" class="form-control" value="565" name="" id="" disabled/>
                    </div>
                    <div class="col-md-6 form-group mb-3">
                        <label>Applying for</label>
                        <input type="text" class="form-control" value="Vacancy" name="" id="" disabled/>
                    </div>
                    <div class="col-md-12 form-group mb-3">
                        <label>Role Summary</label>
                        <textarea name="" id="" class="w-100"
                            placeholder="Lorem Ipsum is simply dummy text of the printing and typesetting industry. Lorem Ipsum has been the industry's standard dummy text ever since the 1500s,"
                            disabled></textarea>
                    </div>
                    <div class="col-md-12 form-group mb-3">
                        <label>Responsiblity</label>
                        <input type="text" class="form-control" value="Lorem Ipsum is simply dummy text." name="" id=""
                            disabled/>
                    </div>
                    <div class="col-md-12 form-group mb-3">
                        <label>Experience</label>
                        <input type="text" class="form-control" value="Fresher" name="" id="" disabled/>
                    </div>
                    <div class="col-md-6 form-group mb-3">
                        <label>Min Qualification</label>
                        <input type="text" class="form-control" value="BCA" name="" id="" disabled/>
                    </div>
                    <div class="col-md-6 form-group mb-3">
                        <label>Preferred Qualification</label>
                        <input type="text" class="form-control" value="Low" name="" id="" disabled/>
                    </div>
                    <div class="col-md-12 form-group mb-3">
                        <label>Certificates</label>
                        <input type="text" class="form-control" value="N/A" name="" id="" disabled/>

                    </div>
                    <div class="col-md-12 form-group mb-3">
                        <label>Skill Needed</label>
                        <input type="text" class="form-control" value="N/A" name="" id="" disabled/>

                    </div>
                    <div class="col-md-12 form-group mb-3">
                        <label>Preferred Domain Knowledge</label>
                        <input type="text" class="form-control" value="Lorem Ipsum Simply Dummy Text Here" name="" id=""
                            disabled/>
                    </div>
                </div>
            </div>
        </div>
        <div class="offcanvas-footer">
            <div class="d-flex align-items-center justify-content-end gap-3">
            <Button btnClassName={"btn btn-secondary"} >
                Back
            </Button>
            </div>
        </div>
    </div>
    </>
  )
}

export default CandidateJobDescription
