import React, { useEffect, useState } from 'react'
import Button from '../../commanComponent/Button'
import moment from 'moment-timezone'
import Input from '../../commanComponent/Input'
import CustRadio from '../../commanComponent/CustRadio'
import CustCheckbox from '../../commanComponent/CustCheckbox'
import { CustomButton } from '../../globalComponents'

const AssessmentForm = (props) => {
    const { handleOpenClose, openModel, selectedInterviewFieldData, interviewStageQuestions, fillFeedBackForm, fillFeedbackFormPending } = props

    const [formValues, setFormValues] = useState({});
    const [overallEvaluation, setOverallEvaluation] = useState('')

    useEffect(()=>{
        if(openModel==false){
            setOverallEvaluation('')
            setFormValues({})
        }
    },[openModel])

    const handleChange = (question, value, isChecked) => {
        setFormValues(prevValues => {
            if (Array.isArray(prevValues[question])) {
                if (isChecked) {
                    return {
                        ...prevValues,
                        [question]: [...prevValues[question], value],
                    };
                } else {
                    return {
                        ...prevValues,
                        [question]: prevValues[question].filter(option => option !== value),
                    };
                }
            } else {
                return {
                    ...prevValues,
                    [question]: value,
                };
            }
        });
    };
    useEffect(() => {
        if (interviewStageQuestions) {
            interviewStageQuestions?.forEach(question => {
                if (question.selection_type === 'multiple' && !formValues[question.id]) {
                    setFormValues(prevValues => ({
                        ...prevValues,
                        [question.question]: []
                    }));
                }
                else {
                    setFormValues(prevValues => ({
                        ...prevValues,
                        [question.question]: ''
                    }));
                }
            });
            // setInterviewId()
        }

    }, [interviewStageQuestions]);

    const renderField = () => {
        return (interviewStageQuestions &&
            interviewStageQuestions.map((question, index) => {
                const { input_type, options, id, question: label, selection_type } = question;

                switch (input_type) {
                    case 'radio':
                        return (
                            <div className="row align-items-center border-bottom py-3" key={id}>
                                <div className="col-md-12">
                                    <span className="text-primary fw-bold text-capitalize">{`Q${index + 1}: ${label} ?`}</span>
                                </div>
                                <div className="col-md-8 mt-2">
                                    {options.map(option => (
                                        <div key={option} className="d-inline-block me-3">
                                            <CustRadio
                                                htmlFor={`${id}-${option}`}
                                                text={option}
                                                name={id}
                                                checked={formValues[label] === option}
                                                handleChange={(e) => handleChange(label, option)}
                                            />
                                        </div>
                                    ))}
                                </div>
                            </div>
                        );

                    case 'select':
                        if (selection_type === 'multiple') {
                            return (
                                <div className="row align-items-center border-bottom py-3" key={id}>
                                    <div className="col-md-12">
                                        <div className="col-md-12">
                                            <span className="text-primary fw-bold text-capitalize">{`Q${index + 1}: ${label} ?`}</span>
                                        </div>
                                        <div className="col-md-12 mt-2 d-flex flex-wrap">
                                            {options.map(option => (
                                                <CustCheckbox
                                                    key={option}
                                                    id={`${id}-${option}`}
                                                    label={option}
                                                    checked={formValues[label]?.includes(option)}
                                                    handleChange={(e) => handleChange(label, option, e.target.checked)}
                                                />
                                            ))}
                                        </div>
                                    </div>
                                </div>
                            );
                        } else {
                            return (
                                <div className="row align-items-center border-bottom py-3" key={id}>
                                    <div className="col-md-12">
                                        <div className="col-md-12">
                                            <span className="text-primary fw-bold text-capitalize">{`Q${index + 1}: ${label} ?`}</span>
                                        </div>
                                        <div className="col-md-12 mt-2">
                                            <div className="selectcOff">
                                                <select
                                                    className="form-select form-select-sm"
                                                    value={formValues[label]||''}
                                                    aria-label="Small select example"
                                                    onChange={(e) => handleChange(label, e.target.value)}
                                                >
                                                    <option value="" disabled selected>Select...</option>
                                                    {options.map(option => (
                                                        <option key={option} value={option}>{option}</option>
                                                    ))}
                                                </select>
                                            </div>
                                        </div>
                                    </div>
                                </div>
                            );
                        }

                    case 'number':
                    case 'text':
                        return (
                            <div className="row align-items-center border-bottom py-3" key={id}>
                                <div className="col-md-12">
                                    <span className="text-primary fw-bold text-capitalize">{`Q${index + 1}: ${label} ?`}</span>
                                </div>
                                <div className="col-md-12 mt-2">
                                    <Input
                                        type={input_type}
                                        placeholder={label}
                                        value={formValues[label]||''}
                                        onChange={(e) => handleChange(label, e.target.value)}
                                    />
                                </div>
                            </div>
                        );

                    default:
                        return null;
                }
            }))

    };
    const evaluation1 = () => {
        return (

            <div className="row align-items-center border-bottom py-3" >
                <div className="col-md-12">
                    <span className="text-primary fw-bold text-capitalize">Overall Evaluation <span className="text-danger">*</span></span>
                </div>
                <div className="col-md-8 mt-2 d-flex ">
                    <CustRadio
                        htmlFor="evaluationApprove"
                        text="Approve"
                        name="ealuation"
                        value="approve"
                        checked={overallEvaluation == "approve" ? true : false}
                        handleChange={e => setOverallEvaluation("approve")}
                    />
                    <CustRadio
                        htmlFor="evaluationReject"
                        text="Reject"
                        name="ealuation"
                        value="reject"
                        handleChange={e => setOverallEvaluation("reject")}
                        checked={overallEvaluation == "reject" ? true : false}
                    />
                </div>
            </div>
        )
    }

    const submitInterviewAssessmentForm = () => {

        const payload = {
            _id: selectedInterviewFieldData?._id,
            feedback_form: formValues,
            interview_status: overallEvaluation
        }
        fillFeedBackForm(payload)

    }



    return (
        <>
            <div className={`offcanvas offcanvas-end creatNewTicketOff ${openModel ? 'show' : ''}`}
                tabIndex={-1}
                style={{
                    visibility: openModel ? 'visible' : 'hidden',
                    width: "800px"
                }}
                id="assessmentForm"
            >
                <div className="offcanvas-header">
                    <h5 className="offcanvas-title  fs-5 fw-bold text-primary">Interview Assessment Form </h5>
                    <button type="button" className="btn-close" data-bs-dismiss="offcanvas" aria-label="Close"
                        onClick={() => handleOpenClose()}
                    />
                </div>
                <div className="offcanvas-body pb-3">
                    <div className="bg-white">
                        <span>Kindly Fill Interview Assessment Form.</span>
                        {/* <strong className="text-primary d-block mb-3">Interview Ratings (on a Scale of 1 (Lowest) to 5
                (Highest))</strong> */}
                    </div>
                    <div className="d-flex justify-content-between typerating mb-3 ">
                        <div className="">
                            <span>Candidate Name <strong>{selectedInterviewFieldData?.candidate_name}</strong></span>
                        </div>

                        <div className="">
                            <span>Date of Assessment <strong>{moment(selectedInterviewFieldData?.interview_assigned_at).format("DD MMM YYYY")}</strong></span>
                        </div>
                    </div>
                    <form>
                        {renderField()}
                        {interviewStageQuestions && evaluation1()}
                    </form>

                </div>
                <div className="offcanvas-footer">
                    <div className="d-flex align-items-center justify-content-end gap-3">
                        <CustomButton
                            text={"Submit"}
                            classNames={"btn btn-org-gradient"}
                            handleClick={() => submitInterviewAssessmentForm()}
                            isLoading={fillFeedbackFormPending}
                        />

                        <Button btnClassName={"btn btn-secondary"}>Back</Button>
                    </div>
                </div>
            </div>
        </>
    )
}

export default AssessmentForm
