import React, { useMemo, useState } from 'react'
import AssessmentForm from '../../modal/hiring/AssessmentForm'
import CandidateJobDescription from '../../modal/hiring/CandidateJobDescription'
import InterviewCandidateDetail from '../../modal/hiring/InterviewCandidateDetail'
import CustomTable from '../../reuseableComponents/customTable/CustomTable'
import moment from 'moment-timezone'
import Image from '../../commanComponent/Image'
import { getBadgeColor } from '../../../utils/common'

const MrfInterviewStatus = (props) => {
    const {
        interviewStatus,
        setInterviewStatus,
        setQueryType,
        assignedInterviewData,
        isLoading,
        currentPage,
        onHandleViewCandidateModal,
        onHandleQuestionsByInterviewModal,
        setCurrentPage
    } = props
    const [activeTab, setActiveTab] = useState(interviewStatus || "interviewPending");
    const [candidateData, setCandidateData] = useState('')

    const handleActiveTab = (type) => {
        if (type === "candidate") {
            setQueryType(type)
            setActiveTab(type);
        } else {
            setInterviewStatus(type)
            setActiveTab(type);
            setQueryType("interview")
        }
    }
    const mrfInterviewPendingApproveFields = [
        { key: 'sn', label: 'S.N' },
        { key: 'mrf_id_value', label: 'MRF ID' },
        { key: 'candidate_id_value', label: 'Candidate ID' },
        { key: 'interview_stage_name', label: 'Interview Round' },
        { key: 'interview_type_name', label: 'Interview Type' },
        { key: 'interview_assigned_at', label: 'Interview Date & Time' },
        { key: 'interview_type_value', label: 'Meeting Link' },
        { key: 'action', label: 'Action' }
    ];

    const mrfCandidateFields = [
        { key: 'sn', label: 'S.N' },
        { key: 'mrf_id_value', label: 'MRF ID' },
        { key: 'candidate_id_value', label: 'Candidate ID' },
        { key: 'candidate_name', label: 'Candidate Name' },
        { key: 'candidate_email', label: 'Email' },
        { key: 'department_name', label: 'Designation' },
        { key: 'designation_name', label: 'Designation' },
        { key: 'interview_status', label: 'Interview Status' },
        { key: 'action', label: 'Action' }
    ];

    const scopedSlotsPending = useMemo(() => ({
        sn: (_, index) => (
            <span>{index + 1}</span>
        ),

        interview_assigned_at: item => moment(item.interview_assigned_at).format("DD-MM-YYYY"),

        action: item => (
            <div className="btn-group actionDrp">
                <button type="button" className="btn dropdown-toggle-split"
                    data-bs-toggle="dropdown" aria-expanded="false">
                    <i className="bi bi-three-dots-vertical"></i>
                </button>
                <ul className="dropdown-menu">
                    <li><span className="dropdown-item"
                        onClick={() => onHandleViewCandidateModal(item?._id)} >View
                        Candidate Details</span>
                    </li>
                    <li><span className="dropdown-item"
                        onClick={() => onHandleQuestionsByInterviewModal(item?.interview_stage,item)}
                    >Fill Form</span>
                    </li>
                </ul>
            </div>
        )
    }), [assignedInterviewData,]);

    const scopedSlotsApprove = useMemo(() => ({
        sn: (_, index) => (
            <span>{index + 1}</span>
        ),

        created_at: item => moment(item.created_at).format("DD-MM-YYYY"),
        action: item => (
            <span
                onClick={() => onHandleViewCandidateModal(item?._id)}
            >
                <Image imgSrc={"images/view-icon.svg"} altText={""} width={32}></Image>
            </span>
        )
    }), [assignedInterviewData,]);

    const scopedSlotsCandidate = useMemo(() => ({
        sn: (_, index) => (
            <span>{index + 1}</span>
        ),
        interview_status: item => (
            <span
                className={`badge ${getBadgeColor(item.interview_status)}`}
            >
                {item.interview_status}
            </span>
        ),

        created_at: item => moment(item.created_at).format("DD-MM-YYYY"),
        action: item => (
            <span
                onClick={() => onHandleViewCandidateModal(item?._id)}
            >
                <Image imgSrc={"images/view-icon.svg"} altText={""} width={32}></Image>
            </span>
        )
    }), [assignedInterviewData,]);


    return (
        <>
            <div className="cardBox myRequest mb-3">
                <div className="cardHead">
                    <img src="images/myrequest-icon.svg" alt="" width="35" />
                    <h3 className="ms-2">Interview Status</h3>
                </div>
                <ul className="nav nav-tabs leaveTabs mb-4">
                    <li className="nav-item">
                        <button
                            className={`nav-link ${activeTab === "pending" ? "active" : ""}`}
                            data-bs-toggle="tab"
                            data-bs-target="#pending"
                            type="button"
                            onClick={() => handleActiveTab("pending")}
                        >
                            Interview Pending
                        </button>
                    </li>
                    <li className="nav-item">
                        <button
                            className={`nav-link ${activeTab === "approve" ? "active" : ""}`}
                            data-bs-toggle="tab"
                            data-bs-target="#approve"
                            type="button"
                            onClick={() => handleActiveTab("approve")}
                        >
                            Interview Completed
                        </button>
                    </li>
                    <li className="nav-item">
                        <button
                            className={`nav-link ${activeTab === "candidate" ? "active" : ""}`}
                            data-bs-toggle="tab"
                            data-bs-target="#candidate"
                            type="button"
                            onClick={() => handleActiveTab("candidate")}
                        >
                            Candidate Reported
                        </button>
                    </li>
                </ul>
                <div className="tab-content">
                    <div className={`tab-pane fade ${activeTab === "pending" ? "show active" : ""}`} id="pending">
                        <div className="table-responsive tableDiv text-nowrap overflowTable">
                            <CustomTable
                                data={assignedInterviewData?.data}
                                fields={mrfInterviewPendingApproveFields}
                                isLoading={isLoading}
                                pagination={true}
                                currentPage={currentPage}
                                setCurrentPage={setCurrentPage}
                                totalPages={assignedInterviewData?.totalPages}
                                isHeader={false}
                                tableStyle={{ padding: "0px", boxShadow: "none" }}
                                scopedSlots={scopedSlotsPending}
                            />
                         
                        </div>
                    </div>
                    <div className={`tab-pane fade ${activeTab === "approve" ? "show active" : ""}`} id="approve">
                        <div className="table-responsive tableDiv text-nowrap">
                            <CustomTable
                                data={assignedInterviewData?.data}
                                fields={mrfInterviewPendingApproveFields}
                                isLoading={isLoading}
                                pagination={true}
                                currentPage={currentPage}
                                setCurrentPage={setCurrentPage}
                                totalPages={assignedInterviewData?.totalPages}
                                isHeader={false}
                                tableStyle={{ padding: "0px", boxShadow: "none" }}
                                scopedSlots={scopedSlotsApprove}
                            />
                         
                        </div>
                    </div>
                    <div className={`tab-pane fade ${activeTab === "candidate" ? "show active" : ""}`} id="reported">
                        <div className="table-responsive tableDiv text-nowrap">
                            <CustomTable
                                data={assignedInterviewData?.data}
                                fields={mrfCandidateFields}
                                isLoading={isLoading}
                                pagination={true}
                                currentPage={currentPage}
                                setCurrentPage={setCurrentPage}
                                totalPages={assignedInterviewData?.totalPages}
                                isHeader={false}
                                tableStyle={{ padding: "0px", boxShadow: "none" }}
                                scopedSlots={scopedSlotsCandidate}
                            />
                          
                        </div>
                    </div>


                </div>
            </div>

            <AssessmentForm />
            <CandidateJobDescription />
            <InterviewCandidateDetail />
        </>
    )
}

export default MrfInterviewStatus
