import React, { useEffect, useRef, useState } from 'react'
import { useDispatch, useSelector } from 'react-redux'
import { MrfInterviewStatus } from '../components/hiring/interviewPanal'
import { mrfInterviewAction } from '../core/resource/actions'
import InterviewCandidateDetail from '../components/modal/hiring/InterviewCandidateDetail'
import AssessmentForm from '../components/modal/hiring/AssessmentForm'
import { pagination } from '../utils/Constants.config'

const InterviewPanel = () => {
  const dispatch = useDispatch()
  const {
    getAssignedInterviewPending,
    getAssignedInterviewRes,
    getAssignedInterviewError,

    getQuestionByInterviewStagePending,
    getQuestionByInterviewStageRes,
    getQuestionByInterviewStageError,

    fillFeedbackFormPending,
    fillFeedbackFormRes,
    fillFeedbackFormError,

    getInterviewDetailsByIdPending,
    getInterviewDetailsByIdRes,
    getInterviewDetailsByIdError,

  } = useSelector(state => state.mrfInterview)
  const [currentPage, setCurrentPage] = useState(1);
  const [queryType, setQueryType] = useState("interview")
  const [interviewStatus, setInterviewStatus] = useState("pending")
  const [assignedInterviewData, setAssignedInterviewData] = useState("pending")
  const [openCandidateModal, setOpenCandidateModal] = useState(false)
  const [openInterviewStageModal, setOpenInterviewStageModal] = useState(false)
  const [selectedInterviewFieldData, setSelectedInterviewFieldData] = useState('')
  const [interviewStageQuestions, setInterviewStageQuestions] = useState('')
  const [candidateIntervewData, setCandidateIntervewData] = useState('')

  // get Assigned Interview
  useEffect(() => {
    getAssignedInterview()
  }, [currentPage, interviewStatus, queryType])

  const getAssignedInterview = () => {
    let params = {
      query_type: queryType,
      page: currentPage,
      limit:pagination.limit,
    }
    if (queryType !== "candidate") {
      params.interview_status = interviewStatus
    }
    dispatch(mrfInterviewAction.getAssignedInterviewRequest(params))
  }


  useEffect(() => {
    if (!getAssignedInterviewPending) {
      if (getAssignedInterviewError) {
        return;
      }
      if (getAssignedInterviewRes) {
        if (getAssignedInterviewRes?.data?.code === 200) {
          const getAssignedInterviewResData = getAssignedInterviewRes?.data?.data?.data
          setAssignedInterviewData(getAssignedInterviewResData)

        }
      }
    }
  }, [
    getAssignedInterviewRes,
    getAssignedInterviewError,
    getAssignedInterviewPending
  ]);

  // get Question By Interview Stage 
  const onHandleQuestionsByInterviewModal = (id,selectedField) => {
    if (id) {
      getQuestionByInterviewStage(id)
      setOpenInterviewStageModal(!openInterviewStageModal)
      setSelectedInterviewFieldData(selectedField)
    }
    setOpenInterviewStageModal(!openInterviewStageModal)
  }

  const getQuestionByInterviewStage  = (id) => {
    const params = {
      interview_stage: id
    }
    dispatch(mrfInterviewAction.getQuestionByInterviewStageRequest(params))

  }


  useEffect(() => {
    if (!getQuestionByInterviewStagePending) {
      if (getQuestionByInterviewStageError) {
        return;
      }
      if (getQuestionByInterviewStageRes) {
        if (getQuestionByInterviewStageRes?.data?.code === 200) {
          const getQuestionByInterviewStageData = getQuestionByInterviewStageRes?.data?.data?.data
          setInterviewStageQuestions(getQuestionByInterviewStageData)

        }
      }
    }
  }, [
    getQuestionByInterviewStageRes,
    getQuestionByInterviewStageError,
    getQuestionByInterviewStagePending
  ]);


  // fill Feedback Form
  const fillFeedBackForm =(payload)=>{
    dispatch(mrfInterviewAction.fillFeedbackFormRequest(payload))
  }

  useEffect(() => {
    if (!fillFeedbackFormPending) {
      if (fillFeedbackFormError) {
        return;
      }
      if (fillFeedbackFormRes) {
        if (fillFeedbackFormRes?.data?.code === 200) {
          setSelectedInterviewFieldData('')
          setOpenInterviewStageModal(!openInterviewStageModal)
          dispatch(mrfInterviewAction.handelClearMrfInterviewStore('HANDLE_CLEAR_FILL_FEEDBACK_FORM_STORE'))
          getAssignedInterview()
        }
      }
    }
  }, [
    fillFeedbackFormRes,
    fillFeedbackFormError,
    fillFeedbackFormPending
  ]);


  // get Interview Details By Id

  const onHandleViewCandidateModal = (id) => {
    if (id) {
      getCandidateInterviewDetailsById(id)
      setOpenCandidateModal(!openCandidateModal)
    }
    setOpenCandidateModal(!openCandidateModal)
  }

  const getCandidateInterviewDetailsById = (id) => {
    const params = {
      _id: id
    }
    dispatch(mrfInterviewAction.getInterviewDetailsByIdRequest(params))

  }

  useEffect(() => {
    if (!getInterviewDetailsByIdPending) {
      if (getInterviewDetailsByIdError) {
        return;
      }
      if (getInterviewDetailsByIdRes) {
        if (getInterviewDetailsByIdRes?.data?.code === 200) {
          const getInterviewDetailsByIdData = getInterviewDetailsByIdRes?.data?.data?.data
          setCandidateIntervewData(getInterviewDetailsByIdData)

        }
      }
    }
  }, [
    getInterviewDetailsByIdRes,
    getInterviewDetailsByIdError,
    getInterviewDetailsByIdPending
  ]);

  return (
    <>
      <MrfInterviewStatus
        setInterviewStatus={setInterviewStatus}
        interviewStatus={interviewStatus}
        setQueryType={setQueryType}
        assignedInterviewData={assignedInterviewData}
        isLoading={getAssignedInterviewPending}
        currentPage={currentPage}
        setCurrentPage={setCurrentPage}
        onHandleViewCandidateModal={onHandleViewCandidateModal}
        onHandleQuestionsByInterviewModal={onHandleQuestionsByInterviewModal}
      />

      <InterviewCandidateDetail
        openModel={openCandidateModal}
        handleOpenClose={onHandleViewCandidateModal}
        candidateIntervewData={candidateIntervewData}

      />
      <AssessmentForm
       openModel={openInterviewStageModal}
       handleOpenClose={onHandleQuestionsByInterviewModal}
       selectedInterviewFieldData={selectedInterviewFieldData}
       interviewStageQuestions={interviewStageQuestions}
       fillFeedBackForm={fillFeedBackForm}
       fillFeedbackFormPending={fillFeedbackFormPending}
      
      />


    </>
  )
}

export default InterviewPanel
