import React from 'react'

const CustRadio = ({htmlFor,text ,name,handleChange,checked,value}) => {
    return (
        <div class="acceptCheck me-2">
            <input type="checkbox"
            id={htmlFor} class="d-none"
            checked={checked}
            value={value}
            name={name}
            onChange={handleChange}
            />
            <label for={htmlFor}>{text||''}</label>
        </div>
    )
}

export default CustRadio
